
import NotFound from '../NotFound/NotFound';
import Pages from '../../components/Pages/Pages';
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

export default {
name: 'BetRecord',
components: {
XHeader,
NotFound,
Pages,
Popup,
Group,
Datetime
},
computed: {},
async created() {
await this.getData();
await this.getUserInfo();
await this.getGameType();
},
data() {
return {
userInfo: {},
state: ['下单成功', '已中奖', '未中奖', '已取消'],
list: [], // info
totalCount: 0, // 总条数
pageSize: 10, // 查询条数
startRow: 0, // 起始条数
queryShow:false,
startDate:this.DataTime.getDay(-1),
endDate:this.DataTime.getDay(0),
queryUserId:false,
gameTypeList:[],
gameNum:0,
award:false,
noAward:false
};
},
methods: {
async getUserInfo() {
console.log(this.$route.params.gameType);
let res = await this.$http('/queryUserCommonInfo', {
body: {
userId: window.localStorage.getItem('userId'),
token: window.localStorage.getItem('token')
}
});
this.userInfo = Object.assign({}, res.returnMap);
},
async getData(v) {
this.$vux.loading.show({
text: 'Loading'
});
if (v != undefined) {
this.startRow = v;
}
let res = await this.$http('/queryUserJoinOrder', {
body: {
startRow: this.startRow,
pageSize: this.pageSize
}
});
this.list = res.returnList;
this.totalCount = res.totalCount;
this.$vux.loading.hide();
},
queryShowPop(){
this.queryShow =!this.queryShow;
/* this.getGameType(); */
},
async getGameType(v) {
this.$vux.loading.show({
text: 'Loading'
});
let res = await this.$http('/queryGameTypeListByParentId', {});
this.gameTypeList = res.returnList;
this.$vux.loading.hide();
},
selectGameType(index){
this.gameNum = index;
},
resetC(){
this.gameNum = 0;
this.award = false;
this.noAward=false;
}
}
};



